<template>
    <ul class="submit_question_container">
        <li class="sq_li-text"></li>
        <li class="sq_li-telNumber sq_li-input"><input type="number" name="telphone"/></li>
        <li class="sq_li-text"><span></span></li>
        <li class="sq_li-telNumber sq_li-input">
            <textarea>

            </textarea>
        </li>
        <li class="sq_li-upload sq_li-input">
            <label><input type="file" hidden><span></span></label>
        </li>
    </ul>
    <button class="sq_button-submit"></button>
</template>
<style lang='sass'>
    @import './../../../static/scss/function.scss';
    .submit_question_container{
        .sq_li-text{
            height:px2em(70px);
            line-height:px2em(70px);
            font-size:px2em(30px);
            text-indent:px2em(16px);
            span{
                color:#be3333;
            }
        }
        .sq_li-input{
            width:100%;
            min-height:px2em(70px);
            border:2px solid $borderColor;
            border-radius:px2em(35px);
            background:rgba(0,0,0,0.3);
            input{
                color:#fff;
                height:px2em(68px);
                width:90%;
                text-indent:20px;
                border:none;
                background:none;
                outline:none;
            }
            textarea{
                width:95%;
                margin:0px 0 0 0 2%;
                height:120px;
                text-indent:none;
                resize:none;
                background:none;
                border:none;
                outline:none;
                color:#fff;

            }
        }
        .sq_li-upload{
            margin-top:px2em(20px);
            height:px2em(70px);
            width:100%;
            span{
                display:block;
                height:px2em(45px);
                background:url('./../../../static/images/add_pic.png') no-repeat left center;
                text-indent:px2em(60px);
                line-height:px2em(45px);
                background-size:px2em(45px) px2em(45px);
                margin:px2em(12px) 0 0 px2em(20px);
            }
        }
    }
    .sq_button-submit{
        margin-top:px2em(20px);
        width:px2em(240px);
        height:px2en(60px);
        background:#3f2c6d;
        text-align:center;
        color:#fff;
        border:none;
        outline:none;
        border-radius:px2em(30px);
        margin-left:50%;
        position:relative;
        left:px2em(-120px);
    }
</style>
<script>
    export default{
        data(){
            return{
                msg:'hello vue'
            }
        }
    }
</script>



