<template>
  <div id="app">
    <a class='app-nav' v-link="{path:'/Genius'}" v-on:click="picChange(1)"><img v-bind:src="record===1?picArrActive[0]:picArr[0]" class="list_pic"/></a>
    <a class='app-nav' v-link="{path:'/Service'}" v-on:click="picChange(2)"><img v-bind:src="record===2?picArrActive[1]:picArr[1]" class="list_pic"/></a>
    <a class='app-nav' v-link="{path:'/Record'}" v-on:click="picChange(3)"><img v-bind:src="record===3?picArrActive[2]:picArr[2]" class="list_pic"/></a>
    <a class='app-nav' v-link="{path:'/Platform'}" v-on:click="picChange(4)"><img v-bind:src="record===4?picArrActive[3]:picArr[3]" class="list_pic"/></a>
  </div>
  <router-view></router-view>
  <div id="slide-bar">
    <div class="slide-top" @touchstart="touchList($event,'slide-top_hover')" @touchend="touchList($event,'slide-top')"></div>
    <div class="slide-bottom" @click="reTop" @touchstart="touchList($event,'slide-bottom_hover')" @touchend="touchList($event,'slide-bottom')"></div>
  </div>
</template>

<script>



export default {
  data () {
    return {
      picArr:['./static/images/gamegenius.png','./static/images/service.png','./static/images/record.png','./static/images/platform.png'],
      picArrActive:['./static/images/gamegenius_active.png','./static/images/service_active.png','./static/images/record_active.png','./static/images/platform_active.png'],
      record:1,
      control_close:0, //
      control_help:false  //
    }
  },
  methods:{
    picChange:function(num){
      this.record = num;
      this.$children[0].$data.control = false;
    },
    reTop:function(){
      this.$nextTick(function(){
          document.querySelector('.wapper').scrollTop = 0;
      })
    },
    touchList:function(event,name){
        event.target.className = name;
        if(name === 'slide-top'){
          if(this.control_close === 1){
            this.$children[0].$data.control = false;
            this.$children[0].$data.control_feedback = false;
            this.$children[0].$data.service_cNumber = 'default_component';
          }else{
            console.log('')
          }
        }
    }
  }
}
</script>

<style lang="sass">
  @import './../static/css/base.css';
  @import './../static/scss/function.scss';
  @import './../static/css/swiper-3.4.0.min.css';

html{
  height:100%;
}
body {
    font-family: Helvetica, sans-serif;
    height:100%;
    overflow:hidden;
    position:relative;
    background:url(./../static/images/background.png) repeat;
}
#app{
  position:absolute;
  top:0;
  left:0;
  width:px2em(160px);
  height:100%;
  background:$darkblue;
    .app-nav{
      position:relative;
      display:block;
      width:100%;
      height:25%;
      color:#fff;
      text-decoration:none;
      text-align:center;
      background:url(./../static/images/left_line.png) repeat-x bottom;
        .list_pic{
          position:absolute;
          top:0;
          left:0;
          bottom:0;
          right:0;
          margin:auto;
          width:px2em(100px);
        }
    }
}
.wapper{
  margin:0 px2em(80px) 0 px2em(160px);
  height:100%;
  overflow:scroll;
}
#slide-bar{
  position:absolute;
  top:0;
  right:0;
  width:px2em(80px);
  height:100%;
  background:black;
    .slide-top{
      margin:px2em(10px) auto;
      width:px2em(60px);
      height:px2em(60px);
      background:url('./../static/images/close.png') no-repeat center;
      background-size:cover;
    }
    .slide-top_hover{
      margin:px2em(10px) auto;
      width:px2em(60px);
      height:px2em(60px);
      background:url('./../static/images/close_active.png') no-repeat center;
      background-size:cover;
    }
    .slide-bottom{
      position:absolute;
      bottom:px2em(16px);
      left:px2em(10px);
      width:px2em(60px);
      height:px2em(60px);
      background:url('./../static/images/reTop.png') no-repeat;
      background-size:cover;
    }
    .slide-bottom_hover{
      position:absolute;
      bottom:px2em(16px);
      left:px2em(10px);
      width:px2em(60px);
      height:px2em(60px);
      background:url('./../static/images/reTop_active.png') no-repeat;
      background-size:cover;
    }
}
.components_container{
  margin:0 auto;
  width:px2em(1051px);
  position:relative;
}
</style>



