<template>
    <dl class="service_default">
        <dt></dt>
        <dd><span @click="dealWithClick('common_problem')"></span><span @click="dealWithClick('common_problem')"></span></dd>
    </dl>
    <dl class="service_default">
        <dt></dt>
        <dd><span @click="dealWithClick('submit_question')"></span><span @click="dealWithClick('submit_question')"></span><span @click="dealWithClick('submit_question')">BUG</span></dd>
    </dl>
</template>
<style lang="sass">
    @import './../../../static/scss/function.scss';
    .service_default{
        margin-bottom:px2em(120px);
    }
    .service_default dt{
        height:px2em(50px);
        font-size:px2em(26px);
        line-height:px2em(50px);
        font-weight:600;
        text-indent:px2em(60px);
        background:url('./../../../static/images/question_mark.png') no-repeat center left;
        background-size:px2em(50px) px2em(50px);
    }
    .service_default dd{
        font-size:px2em(26px);
        text-indent:px2em(70px);
        span{
            font-size:px2em(20px);
            margin:px2em(20px) px2em(60px) 0 0;
            text-indent:0;
            display:inline-block;
            min-width:px2em(180px);
            height:px2em(60px);
            background:#3f2c6d;
            border-radius:px2em(30px);
            text-align:center;
            padding:0 px2em(20px);
            line-height:px2em(60px);
    }
    }
</style>
<script>
    export default{
        data(){
            return{
                msg:'default'
            }
        },
        methods:{
            dealWithClick:function(type){
                this.$parent.$data.service_cNumber = type;
                this.$root.$data.control_close = 1;
            }
        }
    }
</script>



