<template>
    <div id="record" class="wapper">
        <div v-if="!control">
            <div class="record_title">{{computed_title}}</div>
            <div class="components_container">
                <div class="record_response" :class="className_computed" @click="record_info">
                    <span>!</span><br />
                    <em>2016-12-12 18:24</em>
                </div>
            </div>
        </div>
        <div v-if="control" style="color:#fff" class="components_container">
            <div class="question_title question">{{question_object.title}}</div>
            <div class="question_answer question">{{question_object.container}}</div>
            <img />
            <div class="question_response">
                <div class="judge_response" v-if="!control_feedback">
                    <div><span class="yes" @click="response(false)"></span><span class="no" @click="response(true)"></span></div>
                </div>
                <div class="judge_response" v-else>
                    
                </div>
            </div>
        </div>
    </div>
</template>
<style lang="sass">
    @import './../../../static/scss/function.scss';
    #record{
        color:#fff;
        .record_title{
            height:px2em(60px);
            width:100%;
            font-size:px2em(20px);
            line-height:px2em(60px);
            background:url('./../../../static/images/mengban_2.png') no-repeat;
            background-size:cover;
            text-align:center;
        }
        .record_response{
            position:relative;
            margin-top:px2em(20px);
            padding:px2em(10px) px2em(20px);
            border:2px solid #3b405a;
            border-radius:px2em(30px);

            em{
                color:#33374e;
                font-size:px2em(20px);
            }
        }
        .record_response_no{
            background:url('./../../../static/images/question_no.png') 97% center no-repeat rgba(0,0,0,0.2);
            background-size:px2em(50px) px2em(50px);
        }
        .record_response_yes{
            background:url('./../../../static/images/question_yes.png') 97% center no-repeat rgba(0,0,0,0.2);
            background-size:px2em(50px) px2em(50px);
        }
        .question_title{
            margin-top:px2em(30px);
            background:url('./../../../static/images/question_title.png') left center no-repeat;
        }
        .question_answer{
            margin-top:px2em(14px);
            background:url('./../../../static/images/question_answer.png') left center no-repeat;
        }
        .question{
            height:px2em(50px);
            text-indent:px2em(60px);
            background-size:px2em(48px) px2em(48px);
            line-height:px2em(50px);
            font-size:px2em(18px);
    }
        .question_response{
            position:relative;
            top:px2em(30px);
            width:100%;
            height:px2em(110px);
            background:url('./../../../static/images/mengban_2.png') center no-repeat;
            background-size:cover;
            line-height:px2em(110px);
            .judge_response{
                text-indent:px2em(120px);
                div{ text-indent:0;
                    width:px2em(416px);
                    height:px2em(58px);
                    line-height:px2em(58px);
                    position:absolute;
                    top:0;left:0;right:0;bottom:0;
                    margin:auto;
                    span{
                        height:100%;
                        width:px2em(185px);
                        text-align:center;
                        background:url('./../../../static/images/response.png') no-repeat;
                        background-size:cover;
                        font-size:px2em(28px);
                        &.yes{
                            float:left;
                        }
                        &.no{
                            float:right;
                        }
                    }
                }
            }
        }
    }
</style>
<script>
    export default{
        data(){
            return{
                msg:'hello vue',
                requstion:false,
                control:false,
                control_feedback:false,
                question_object:{title:'111',container:'222222222'}
            }
        },
        computed:{
            computed_title:function(){
                return "" + 2 + "" + 1 + ""
            },
            className_computed:function(){
                var cl = '';
                if(this.requstion){
                    cl = 'record_response_yes';
                }else{
                    cl = 'record_response_no';
                }
                return cl;
            }
        },
        methods:{
            record_info:function(){
                this.$root.$data.control_close = 1;
                this.control = true;
            },
            response:function(){
                this.control_feedback = true;
            }
        }
    }
</script>



