<template>
    <div class="gameHelp_contaniner">
        <div class="gameHelp_banner">
            <div class='swiper-wrapper'>
                <img class="swiper-slide" v-for="item in bannerPicArr"  :src="item" />
            </div>
        </div>
        <div class="gameHelp_select">
            <div class="gameHelp_selct_info">
                <input tyle="text" v-model="sercheText" placeholder=""/>
                <button @click="serche" @touchstart="touchList($event,'button')" @touchend="touchList($event,'button')"></button>
            </div>
            <div class="gameHelp_select_list">
                <span v-for="(index,item) in resolveArr" @click="serche_info(item)" class="gameHelp_list_border"><em @touchstart="touchList($event,'em')" @touchend="touchList($event,'em')">{{ item }}</em></span>
                <div v-if='noinfo_dialog' class="noinfo_dialog">
                    <span class="infor_dialog"> </span><br />
                    <a style="color:#ffbf21;" class='app-nav' v-link="{path:'/Service'}" @click="picChange(1)"></a>
                </div>
            </div>
        </div>
    </div>
</template>
<style lang='sass'>
    @import './../../../static/scss/function.scss';
    .gameHelp_contaniner{
        color:#ffffff;
            .gameHelp_banner{
                width:px2em(1050px);
                height:px2em(480px);
                overflow:hidden;
                   img{
                       display:block;
                       width:100%;
                       border:$borderLine;
                       border-radius:px2em(24px);
                    }
            }
            .gameHelp_select{
                margin-top:px2em(20px);
                clear:both;
                .gameHelp_selct_info{
                    height:px2em(60px);
                    border:$borderLine;
                    border-radius:px2em(30px);
                    background:$darkblue;
                    padding:1px;
                }
                input{
                    height:100%;
                    width:px2em(700px);
                    line-height:px2em(40px);
                    color::#fff;
                    text-indent:20px;
                    outline:none;
                    background:none;
                    border:none;
                }
                button{
                    float:right;
                    margin:px2em(2px);
                    height:px2em(56px);
                    width:px2em(180px);
                    font-size:10px;
                    background:url('./../../../static/images/sercheBtn.png') no-repeat;
                    background-size: cover;
                    color:#ffffff;
                    border:none;
                    outline:none;
                    line-height:px2em(30px);
                }
                .button_hover{
                    background:url('./../../../static/images/sercheBtn_active.png') no-repeat;
                    background-size:cover;
                }
                .gameHelp_select_list{
                    margin:px2em(16px) 0 px2em(20px) 0;
                    overflow:hidden;
                }
                .gameHelp_list_border{
                    float:left;
                    width:px2em(343px);
                    height:px2em(50px);
                    border:$borderLine;
                    text-align:center;
                    em{
                        display:block;
                        margin:px2em(3px);
                        line-height:px2em(44px);
                        background:url('./../../../static/images/listBtn.png') no-repeat;
                        background-size:cover;
                    }
                    .em_hover{
                        background:url('./../../../static/images/listBtn_active.png') no-repeat;
                    }
                }
                .noinfo_dialog{
                    margin-left:px2em(15px);
                    color:#fff;
                    font-size:px2em(20px);
                    line-height:2;
                    a{
                        text-decoration:underline;
                    }
                }
                .infor_dialog{
                    margin-bottom:px2em(10px);
                    color:#be3333;
                    font-size:px2em(20px);
                }
            }
    }
</style>
<script>
    import swiper from 'swiper';
    export default{
        data(){
            return{
                bannerPicArr:['./static/images/banner_01.png','./static/images/banner_02.png'],
                sercheText:'',
                listArr:['aaaa','bbb'],
                oldListArr:['asdf','asdf1','gdfgfdsg','dafgadgf','dagfdgdghfd','dgffsdfgs','gfhfdhfd','asfagfdhg','adfgdheerh','adfgdfhjbadf','cxbvxfgsh','cvbxcfhgsh'],
                resolveArr:[],
                noinfo_dialog:false
            }
        },
        methods:{
            serche:function(){
                //aldListArr
                if(this.sercheText !== ''){
                    var arr = [],
                    oldArr = this.oldListArr,
                    i = 0,
                    text = this.sercheText;
                    for(;i<oldArr.length;i++){
                        var count = 0,
                            j = 0;
                        for(;j<text.length;j++){
                            if(oldArr[i].indexOf(text[j]) >= 0){
                                count++;
                            }
                        }
                        arr[i] = count + '_' + oldArr[i];
                    }

                    //
                    var k = 0;
                    this.resolveArr = [];
                    for(;k<text.length;k++){
                        var num = text.length - k;
                        for(var i = 0;i<arr.length;i++){
                            if(arr[i].split('_')[0] == num && num != 0){
                                this.resolveArr.push(arr[i].split('_')[1])
                            }
                        }
                    }
                    if(this.resolveArr.length === 0){
                        this.noinfo_dialog = true;
                    }else{
                        this.noinfo_dialog = false;
                    }
                }else{
                    this.resolveArr = this.oldListArr;
                    this.noinfo_dialog = false;
                }



            },
            serche_info:function(name){
                this.$parent.$data.question_object = {title:'',container:"2",imgUrl:'',type:'1'};
                this.$root.$data.control_help = true;
                this.$parent.$data.control = true;
                this.$root.$data.control_close = 1;
            },
            touchList:function(event,type){
                if(!event.target.className || event.target.className === ''){
                    event.target.className = type + '_hover';
                }else{
                    event.target.className = '';
                }
            },
            picChange:function(num){
                this.$parent.$parent.$data.record = 2;
            }
        },
        ready:function(){
            this.resolveArr = this.oldListArr;

            //banner
            if(this.bannerPicArr.length > 1){
                this.$nextTick(function(){
                    setTimeout(function(){
                        var mySwiper = new swiper('.gameHelp_banner',{
                               autoplay:3000,
                               loop:true,
                               effect:'flip'
                        })
                    },500)

                })

            }else{
                console.log('')
            }
        },
        init:function(){
            console.warn('!!!!!!!!!!')
        }
    }
</script>



